import React, { useEffect, useState } from "react";
import Pagination from "../../components/Pagination";
import Item from "./child/Item";
import { Link } from "react-router-dom";
import Delete from "./child/Delete";
import ModalUID from "../../components/ModalUID";

export default function StaffPage() {
  const [state, setState] = useState({
    modal: {
      componentAction: null,
      title: "",
      method: null,
    },
  });

  const { modal } = state;

  function handleDelete() {
    setState({
      ...state,
      modal: {
        componentAction: <Delete></Delete>,
        title: "Xóa bài viết",
        method: "REMOVE",
      },
    });
  }

  return (
    <div className="container-fluid">
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">Bài viết</h4>{" "}
        <div style={{ display: "flex" }}>
         
          <Link
            className="btn btn-info btn-icon-split btn-sm show"
            to="/news/create?key=news"
          >
            <span className="icon text-white-50">
              <i className="fas fa-plus" />
            </span>
            <span className="text">Thêm bài viết</span>
          </Link>
        </div>
      </div>

      <div className="card shadow mb-4">
        <div className="card-header py-3">
          <div className="row" style={{ justifyContent: "space-between" }}>
            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm bài viết"
                  
                  style={{ maxWidth: "400px", minWidth: "300px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
          </div>
        </div>
        <div className="card-body">
          <div className="table-responsive">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
              <table
                className="table no-footer dataTable"
                id="dataTable"
                width="100%"
                cellSpacing={0}
                aria-describedby="dataTable_info"
                style={{ width: "100%" }}
              >
                <thead>
                  <tr>
                    <th
                      className="sorting_disabled sorting_asc"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="STT"
                    >
                      STT
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Hình ảnh
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Họ và tên"
                    >
                      Tên bài viết
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Tên tài khoản"
                    >
                      Mô tả ngắn
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Số điện thoại"
                    >
                      Trạng thái
                    </th>
               

                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      aria-label="Hành động"
                    >
                      Hành động
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <Item handleDelete={handleDelete}></Item>
                  <Item handleDelete={handleDelete}></Item>
                </tbody>
              </table>
              <Pagination></Pagination>
            </div>
          </div>
        </div>
      </div>
      <ModalUID method={modal.method} title={modal.title}>
        {" "}
        {modal.componentAction}{" "}
      </ModalUID>
    </div>
  );
}
