import React, { useEffect } from "react";
import { Link } from "react-router-dom";

export default function Item(props) {
  return (
    <tr className="hover-product even">
      <td className="sorting_1">2</td>
      <td className="sorting_1">
      <img src="https://data1.ikitech.vn/public/api/SahaImages/APXbZfLMXm1637218585.jpg" class="img-responsive" alt="Image" width="100px" height="100px"/>
      </td>

      <td >
      <Link
          to="/news/edit/1?key=news"
        >
          Bài viết 1
        </Link>
      </td>
      <td>Mô tả ngắn bài viết 1...</td>
      <td style={{color: "green"}}>Đang hiển thị</td>


      <td style={{ display: "flex" }}>
        {/* <Link
          className="btn btn-primary btn-sm show"
          to="/news/comments"
        >
          <i className="fas fa-comment" /> Bình luận
        </Link> */}
        <button
          onClick={props.handleDelete
          }
          data-toggle="modal"
          data-target="#modalUID"
          className="btn btn-danger btn-sm show"
          style={{ marginLeft: "10px" }}
        >
          <i className="fa fa-trash" /> Xóa
        </button>
      </td>
    </tr>
  );
}
